import { Input, ContentChildren, QueryList } from '@angular/core';
import { CellOptions } from '../ooxml/cell-options.interface';

/**
 * @hidden
 */
export class ColumnBase {
    /**
     * The title of the column.
     */
    @Input()
    public title: string;

    /**
     * The width of the column in pixels.
     */
    @Input()
    public width: number;

    /**
     * Toggles the locked (frozen) state of the column.
     *
     * @default false
     */
    @Input()
    public locked: boolean;

    /**
     * Sets the visibility of the column.
     *
     * @default false
     */
    @Input()
    public hidden: boolean;

    /**
     * The options of the column header cell.
     */
    @Input()
    public headerCellOptions: CellOptions;

    /**
     * @hidden
     */
    @ContentChildren(ColumnBase)
    public children: QueryList<ColumnBase>;

    /**
     * @hidden
     */
    public get level(): number {
        return this.parent ? this.parent.level + 1 : 0;
    }

    constructor(public parent?: ColumnBase) {}
}



